<template>
  <div id="app">
    <div class="topbar">
      <div id="scroll-line"></div>
    </div>
    <!-- TODO: change the following nav into a new nav component -->
    <NavBar></NavBar>
    <MainSection></MainSection>
    <Product></Product>
    <Introduction v-on:scroll.native="handleScroll"></Introduction>
    <Process></Process>
    <TeamMembers></TeamMembers>
    <Footer></Footer>
  </div>
</template>

<script>
// @ is an alias to /src
import NavBar from "@/components/NavBar.vue";
import MainSection from "@/components/MainSection.vue";
import Product from "@/components/Product.vue";
import Introduction from "@/components/Introduction.vue";
import TeamMembers from "@/components/TeamMembers.vue";
import Footer from "@/components/Footer.vue";
import Process from "@/components/Process.vue";
import $ from "jquery";
import velocity from "velocity-animate";

export default {
  name: "App",
  components: {
    NavBar,
    MainSection,
    Product,
    Introduction,
    Process,
    TeamMembers,
    Footer
  },
  data() {
    return {
      scrolled: false
    };
  },
  methods: {
    vh(v) {
      var h = Math.max(
        document.documentElement.clientHeight,
        window.innerHeight || 0
      );
      return (v * h) / 100;
    },

    handleScroll() {
      var wintop = $(window).scrollTop(),
        docheight = $(document).height(),
        winheight = $(window).height();
      var scrolled = (wintop / (docheight - winheight)) * 100;
      $("#scroll-line").css("width", scrolled + "%");
      // $(".features")
      //   .velocity("scroll", 1000)
      //   .velocity({ opacity: 1 });
      // velocity(
      //   $(".features"),
      //   {
      //     opacity: 1
      //   },
      //   {
      //     scroll: 1000000000
      //   }
      // );
      console.log(wintop);
      var element1 = document.getElementById("tailored");
      var element2 = document.getElementById("metals");
      var element3 = document.getElementById("acronis");
      if (window.scrollY / this.vh(100) < 2.47) {
        element1.classList.add("selected1");
        element2.classList.remove("selected3");
        element3.classList.remove("selected2");
      } else if (window.scrollY / this.vh(100) > 3.46) {
        element1.classList.remove("selected1");
        element2.classList.add("selected3");
        element3.classList.remove("selected2");
      } else {
        element1.classList.remove("selected1");
        element2.classList.remove("selected3");
        element3.classList.add("selected2");
      }
      var element1 = document.getElementById("challenge");
      var element2 = document.getElementById("research");
      var element3 = document.getElementById("design");
      if (window.scrollY / this.vh(100) < 5.42) {
        element1.classList.add("selected1");
        element2.classList.remove("selected2");
        element3.classList.remove("selected3");
      } else if (window.scrollY / this.vh(100) > 7.12) {
        element1.classList.remove("selected1");
        element2.classList.remove("selected2");
        element3.classList.add("selected3");
      } else {
        element1.classList.remove("selected1");
        element2.classList.add("selected2");
        element3.classList.remove("selected3");
      }
    }
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  }
};
</script>

